/* STYLE */
.roundCheckBox {
  cursor: pointer;
  padding: 0.25em 0;
}
.roundCheckBox:hover .label {
    color: #fff;
}
.roundCheckBox--block {
  display: block;
}
.roundCheckBox > input[type='checkbox'] {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  pointer-events: none;
}
.roundBox {
  position: relative;
  display: inline-block;
  width: 0.8em;
  height: 0.8em;
  border: 2px solid #395374;
  border-radius: 50%;
  vertical-align: middle;
}
.roundBox--checked::after {
  content: '';
  position: absolute;
  top: 0.16em;
  left: 0.16em;
  background: #15a4fa;
  width: 0.48em;
  height: 0.48em;
  border-radius: 50%;
}
.label {
  color: #7590b4;
  font-weight: 900;
  padding: 0 1em;
  vertical-align: middle;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}



